<template>
  <div class="popover-palavra"
    v-if="individuos"
    @touchstart="touching = true"
    @touchmove="movePopover($event)"
    @touchend="touching = false"
    @mouseup="touching = false"
    @mousemove="movePopover($event)"
    @mousedown="touching = true"
    >
    <div :class="['popover-individuo', individuo.com_a_palavra ? 'com_a_palavra': 'aparteante']" v-for="individuo in individuos" :key="`popover-individuo-${individuo.id}`">
      <div class="popover-header">
        <div class="arrow"></div>
        <h3
          class="popover-title"
          v-if="individuo.com_a_palavra"
        >
          <i class="fas fa-expand-alt"></i>
          <span>
            Com a palavra
          </span>
        </h3>
        <h3 class="popover-title" v-if="individuo.aparteado">Em aparte</h3>
      </div>
      <div class="popover-body">
        <div v-if="individuo" class="individuo-info">
          <div class="info">
            <div class="name">{{ individuo.nome }}</div>
            <div class="timer mt-1" v-if="individuo.cronometro">
              <cronometro-base
                :cronometro_id="individuo.cronometro"
                :key="`cronometro-popover-${individuo.cronometro}`"
                :ref="`cronometro-popover-${individuo.cronometro}`"
                css_class_controls="d-none"
                css_class="cron-popover-palavra"
                :display_initial="'remaining'"
                :display_format="'mm:ss'"
                :display_size="'2em'"
                :controls="[]"
              ></cronometro-base>
            </div>
          </div>
          <div class="foto me-2"
            v-if="fotografiaUrl(individuo)"
            :style="{ backgroundImage: 'url(' + fotografiaUrl(individuo) + ')' }"></div>
          <div class="foto me-2" v-else>
            <i class="fas fa-user"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import CronometroBase from '../../components/cronometros/CronometroBase.vue'
import Vuex from 'vuex'
export default {
  name: 'popover-palavra',
  components: {
    CronometroBase
  },
  data () {
    return {
      touching: false,
      moving: false
    }
  },
  watch: {
    wsConnected: {
      immediate: true,
      handler (newVal) {
        if (newVal) {
          // WebSocket connected
        } else {
          // WebSocket disconnected
          this.$nextTick(() => {
            this.invalidateCacheModels({
              app: 'painelset',
              models: [
                'individuo',
                'cronometro'
              ]
            })
          })
        }
      }
    }
  },
  computed: {
    individuo_com_a_palavra: function () {
      if (this.data_cache?.painelset_individuo) {
        return Object.values(this.data_cache.painelset_individuo).find(i => i.com_a_palavra)
      }
      return null
    },
    individuo_aparteante: function () {
      if (this.data_cache?.painelset_individuo) {
        return Object.values(this.data_cache.painelset_individuo).find(i => i.aparteado)
      }
      return null
    },
    individuos: function () {
      const individuos = []
      if (this.individuo_aparteante) {
        individuos.push(this.individuo_aparteante)
      }
      if (this.individuo_com_a_palavra) {
        individuos.push(this.individuo_com_a_palavra)
      }
      return individuos
    }
  },
  mounted () {
    this.registerModels({
      app: 'painelset',
      models: ['individuo', 'cronometro']
    })
  },
  methods: {
    ...Vuex.mapActions('store__sync', [
      'invalidateCacheModels'
    ]),
    fotografiaUrl (individuo) {
      if (individuo.parlamentar) {
        return '/api/parlamentares/parlamentar/' + individuo.parlamentar + '/fotografia.c96.png'
      } else if (individuo.fotografia) {
        return '/api/painelset/individuo/' + individuo.id + '/fotografia.c96.png'
      }
      return null
    },
    movePopover (event) {
      if (!this.touching) return

      const popover = this.$el
      const rect = popover.getBoundingClientRect()

      // se for touch, detectar dois toques e alterar fontSize de acordo com a distancia.
      if (event.touches && event.touches.length > 1) {
        event.preventDefault() // prevent scrolling
        const distance = Math.hypot(
          event.touches[0].clientX - event.touches[1].clientX,
          event.touches[0].clientY - event.touches[1].clientY
        )
        const newFontSize = Math.min(Math.max(distance / 10, 10), 100) // constrain between 10px and 60px
        popover.style.fontSize = `${newFontSize}px`
        return
      }

      // Get pointer position (mouse or touch)
      const clientX = event.touches ? event.touches[0].clientX : event.clientX
      const clientY = event.touches ? event.touches[0].clientY : event.clientY

      // Calculate new position (center popover on cursor)
      const newRight = window.innerWidth - clientX - rect.width / 2
      const newBottom = window.innerHeight - clientY - rect.height / 2

      // Constrain to viewport
      const constrainedRight = Math.max(0, Math.min(newRight, window.innerWidth - rect.width))
      const constrainedBottom = Math.max(0, Math.min(newBottom, window.innerHeight - rect.height))

      popover.style.right = `${constrainedRight}px`
      popover.style.bottom = `${constrainedBottom}px`
    }
  }
}
</script>

